import Usercentrics, { DefaultUISettings, TCFUISettings, UI_VARIANT } from '@usercentrics/cmp-browser-sdk';
import { areServicesFull, useCategoryContext } from '../contexts/CategoryContext';
import { useEffect, useState } from 'preact/hooks';
import { EmbedSettingNames as EmbedSettingNamesGDPR } from '../embeddings/gdprEssentials';
import { EmbedSettingNames as EmbedSettingNamesTCF } from '../embeddings/tcfEssentials';
import { getCurrentUiCategoriesChoices } from '../App.helpers';
import { useSettingsContext } from '../contexts/SettingsContext';
import { useUiContext } from '../contexts/UiContext';

/**
 * EMBEDDINGS
 * If embeddings are enabled, the host website will be searched for any html elements with the 'uc-embed' class name,
 * Information on the specified services will then be rendered inside these containers.
 */
export const useEmbeddings = (UC: Usercentrics): void => {
  const [initialized, setInitialized] = useState(false);
  const { categories, services, setCategories } = useCategoryContext();
  const { settings } = useSettingsContext();
  const { uiVariant } = useUiContext();
  const tcfData = UC.getTCFData();

  const tcfContainers = Array.from(document.getElementsByClassName(EmbedSettingNamesTCF.embedTCFAnkerName));
  const serviceContainers = Array.from(
    document.getElementsByClassName(EmbedSettingNamesGDPR.embeddingContainerClassName),
  );

  useEffect(() => {
    if (
      (serviceContainers.length > 0 || tcfContainers.length > 0) &&
      Array.isArray(categories) &&
      categories.length > 0 &&
      !initialized
    ) {
      setInitialized(true);
      UC.loadServices().then(async () => {
        const cat = await UC.getCategoriesFullInfo();
        setCategories(getCurrentUiCategoriesChoices(cat, categories));
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [categories, initialized]);

  useEffect(() => {
    if (settings?.ui?.isEmbeddingsEnabled && uiVariant != null && areServicesFull(services)) {
      setInitialized(true);

      if (tcfContainers.length > 0 && settings.isTcfEnabled && tcfData) {
        import('../embeddings/tcf').then((embedTCF) => {
          embedTCF.embedTCF(tcfData, services, categories, settings.ui as TCFUISettings, tcfContainers);
        });
      }

      if (serviceContainers.length > 0 && Array.isArray(categories) && categories.length > 0) {
        const labels =
          uiVariant === UI_VARIANT.TCF
            ? (settings.ui as TCFUISettings).labels.nonTCFLabels
            : (settings.ui as DefaultUISettings).labels;
        const servicesLabel =
          uiVariant === UI_VARIANT.TCF
            ? 'Services'
            : (settings.ui as DefaultUISettings).secondLayer.tabs.services.label;
        // TODO: Check if there is anything we need to do for CCPA here. Passing the labels correctly probably (from CCPAUISettings)
        import('../embeddings/gdpr').then((embedServices) => {
          embedServices.embedGDPR(categories, services, labels, servicesLabel, serviceContainers);
        });
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [uiVariant, categories, services, settings]);
};
