import { ANALYTICS_TYPE, ERROR_MESSAGES, GLOBAL_EVENTS, VIEW } from '../enums';
import { RTL_LANGUAGES, UC_UI_GLOBAL_NAMESPACE } from '../constants';
import { StateUpdater, useCallback, useEffect, useState } from 'preact/hooks';
import { Theme, generateTheme } from '../styles';
import Usercentrics, {
  BaseService,
  InitialUIValues,
  Service,
  Settings,
  TCFVendor,
  TCF_DECISION_UI_LAYER,
  TRACKING_EVENT_TYPE,
  UI_VARIANT,
} from '@usercentrics/cmp-browser-sdk';
import { dispatchAnalyticsEvent, setTrackingPixel } from '../modules/helpers';
import { TAB_VIEW } from '../enums/Tabs';
import { getCurrentUiCategoriesChoices } from '../App.helpers';
import { useCategoryContext } from '../contexts/CategoryContext';
import { useMutation } from '.';
import { useSettingsContext } from '../contexts/SettingsContext';
import { useTcfContext } from '../contexts/TcfContext';
import { useUiContext } from '../contexts/UiContext';

interface UsePublicMethodsProps {
  initialUIValues: InitialUIValues;
  onThemeChange: StateUpdater<Theme>;
  restartCMP: () => Promise<void>;
  UC: Usercentrics;
}

interface UsePublicMethodsReturn {
  acceptAllDefault: () => Promise<void>;
  acceptAllTCF: () => Promise<void>;
  denyAllDefault: () => Promise<void>;
  denyAllTCF: () => Promise<void>;
  closeCMP: () => Promise<void>;
  showSecondLayer: () => Promise<void>;
  updateLanguage: (lang: string) => Promise<void>;
}

/**
 * GLOBALLY EXPOSED METHODS
 * Offer global methods for plain HTML/JS websites. Those methods are added to the Browser's window object under the
 * UC_UI namespace. E.g. to open the secondLayer from the dev console you can type UC_UI.showSecondLayer().
 */
const usePublicMethods = ({
  initialUIValues,
  onThemeChange,
  restartCMP,
  UC,
}: UsePublicMethodsProps): UsePublicMethodsReturn => {
  const [isUIInitialized, setIsUIInitialized] = useState(false);
  const [shouldDispatchCustomEvent, setShouldDispatchCustomEvent] = useState(true);
  const [, setToggleCcpaChecked] = useState(false);

  const {
    clearServiceVendorScrollToId,
    closeView,
    isAmpEnabled,
    setIsInFullScreen,
    setLanguage,
    setScrollToIdTabViewServiceVendor,
    setTabView,
    setView,
    settingsCloseView,
    uiVariant,
    updateView,
    view,
  } = useUiContext();
  const { categories, getUpdatedCategories, setCategories } = useCategoryContext();
  const { setTcfData } = useTcfContext();
  const { settings, setSettings } = useSettingsContext();

  const [dismissAmp, { loading: dismissAmpLoading }] = useMutation(UC.dismissAmp, UC);
  const [changeLanguage, { loading: changeLanguageLoading }] = useMutation(UC.changeLanguage, UC);
  const [saveOptOutForCcpa, { loading: saveOptOutForCcpaLoading }] = useMutation(UC.saveOptOutForCcpa, UC);
  const [updateServices, { loading: updateServicesLoading }] = useMutation(UC.updateServices, UC);
  const [acceptAllServices, { loading: acceptAllServicesLoading }] = useMutation(UC.acceptAllServices, UC);
  const [acceptAllForTCF, { loading: acceptAllForTCFLoading }] = useMutation(UC.acceptAllForTCF, UC);
  const [acceptAllAmp, { loading: acceptAllAmpLoading }] = useMutation(UC.acceptAllAmp, UC);
  const [denyAllServices, { loading: denyAllServicesLoading }] = useMutation(UC.denyAllServices, UC);
  const [denyAllForTCF, { loading: denyAllForTCFLoading }] = useMutation(UC.denyAllForTCF, UC);
  const [denyAllAmp, { loading: denyAllAmpLoading }] = useMutation(UC.denyAllAmp, UC);

  const isInteractionAnalyticsEnabled = settings.interactionAnalyticsEnabled;

  useEffect(() => {
    setIsUIInitialized(true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  /**
   * A method to accept all default services
   */
  const acceptAllDefault = useCallback(async (): Promise<void> => {
    if (acceptAllServicesLoading) {
      return;
    }

    acceptAllServices().then(async () => {
      if (isAmpEnabled) {
        await acceptAllAmp();
      }
      dispatchAnalyticsEvent(view, ANALYTICS_TYPE.ACCEPT_ALL);
      setTrackingPixel(TRACKING_EVENT_TYPE.ACCEPT_ALL, UC.setTrackingPixel, isInteractionAnalyticsEnabled);
      setCategories(await getUpdatedCategories(view));
    });
  }, [
    UC,
    acceptAllAmp,
    acceptAllServices,
    acceptAllServicesLoading,
    isAmpEnabled,
    isInteractionAnalyticsEnabled,
    getUpdatedCategories,
    setCategories,
    view,
  ]);

  /**
   * A method to accept all tcf consents
   */
  const acceptAllTCF = useCallback(async (): Promise<void> => {
    if (acceptAllServicesLoading || acceptAllForTCFLoading || acceptAllAmpLoading) {
      return;
    }

    const fromLayer =
      view === VIEW.FIRST_LAYER ? TCF_DECISION_UI_LAYER.FIRST_LAYER : TCF_DECISION_UI_LAYER.SECOND_LAYER;

    // For TCF we need to wait for both the services and the vendors/purposes to be updated before continuing.
    Promise.all([acceptAllServices(), acceptAllForTCF(fromLayer)]).then(async () => {
      if (isAmpEnabled) {
        await acceptAllAmp();
      }
      dispatchAnalyticsEvent(view, ANALYTICS_TYPE.ACCEPT_ALL);
      setTrackingPixel(TRACKING_EVENT_TYPE.ACCEPT_ALL, UC.setTrackingPixel, isInteractionAnalyticsEnabled);
      setCategories(await getUpdatedCategories(view));
      setTcfData(UC.getTCFData());
    });
  }, [
    UC,
    acceptAllAmp,
    acceptAllAmpLoading,
    acceptAllForTCF,
    acceptAllForTCFLoading,
    acceptAllServices,
    acceptAllServicesLoading,
    isAmpEnabled,
    isInteractionAnalyticsEnabled,
    getUpdatedCategories,
    setCategories,
    setTcfData,
    view,
  ]);

  /**
   * A method to accept or deny and close CCPA CMP
   * @param isAccept decision type
   */
  const acceptDenyCcpa = useCallback(
    async (isAccept: boolean): Promise<void> => {
      if (saveOptOutForCcpaLoading) {
        return;
      }
      const isOptedOut = !isAccept;

      setToggleCcpaChecked(isOptedOut);
      saveOptOutForCcpa(isOptedOut);
    },
    [saveOptOutForCcpa, saveOptOutForCcpaLoading],
  );

  /**
   * A method for accepting or denying a single service. Is currently used by the embeddings feature and Usercentric's
   * Smart Blocker/Privacy Shield
   * @param serviceId the id of the service
   * @param isAccept decision type
   */
  const acceptDenyService = useCallback(
    async (serviceId: string, isAccept: boolean): Promise<void> => {
      if (!serviceId || typeof serviceId !== 'string') {
        throw new Error(ERROR_MESSAGES.SERVICE_MISSING_ID);
      }

      if (updateServicesLoading) {
        return;
      }

      updateServices([{ serviceId, status: isAccept }]).then(async () => {
        dispatchAnalyticsEvent(view, ANALYTICS_TYPE.SAVE);
        setTrackingPixel(TRACKING_EVENT_TYPE.CUSTOM_CHOICE, UC.setTrackingPixel, isInteractionAnalyticsEnabled);
        setCategories(await getUpdatedCategories(view));
      });
    },
    [
      UC,
      getUpdatedCategories,
      isInteractionAnalyticsEnabled,
      setCategories,
      updateServices,
      updateServicesLoading,
      view,
    ],
  );

  /**
   * A method to accept all consents
   * If UI_VARIANT not defined, method returns null
   */
  const acceptAllConsents = useCallback((): Promise<void> | null => {
    switch (initialUIValues.variant) {
      case UI_VARIANT.CCPA:
        return acceptDenyCcpa(true);
      case UI_VARIANT.DEFAULT:
        return acceptAllDefault();
      case UI_VARIANT.TCF:
        return acceptAllTCF();
      default:
        return null;
    }
  }, [acceptAllDefault, acceptAllTCF, acceptDenyCcpa, initialUIValues.variant]);

  /**
   * A method to check if all consents were accepted
   * Will check purposes, services and vendors consents for TCF
   * And check only services for non-TCF
   */
  const areAllConsentsAccepted = useCallback((): boolean => {
    return UC.areAllConsentsAccepted();
  }, [UC]);

  /**
   * A method to clear localStorage
   */
  const clearStorage = useCallback((): Promise<void> => UC.clearStorage(), [UC]);

  /**
   * This method should always be triggered when clicking on close button on the second layer of Default UI and CCPA.
   */
  const closeCMP = useCallback(async (): Promise<void> => {
    if (dismissAmpLoading) {
      return;
    }

    if (isAmpEnabled && (view !== VIEW.SECOND_LAYER || settingsCloseView !== VIEW.FIRST_LAYER)) {
      await dismissAmp();
      setIsInFullScreen(false);
    }

    if (view === VIEW.SECOND_LAYER) {
      // The settingsCloseView always provides a reference to the view that the secondLayer was opened from. If the
      // secondLayer was opened from the firstLayer, then we will go back to the firstLayer (the user has to make a
      // choice!). If the secondLayer was opened from the Privacy Button / using a button from the host website, we will
      // go back to the Privacy Button View / no view respectively.
      updateView(settingsCloseView);
    } else {
      closeView();
    }
  }, [closeView, dismissAmp, dismissAmpLoading, isAmpEnabled, setIsInFullScreen, settingsCloseView, updateView, view]);

  /**
   * A method to deny all default services
   */
  const denyAllDefault = useCallback(async (): Promise<void> => {
    if (denyAllServicesLoading || denyAllAmpLoading) {
      return;
    }

    denyAllServices().then(async () => {
      if (isAmpEnabled) {
        await denyAllAmp();
      }
      dispatchAnalyticsEvent(view, ANALYTICS_TYPE.DENY_ALL);
      setTrackingPixel(TRACKING_EVENT_TYPE.DENY_ALL, UC.setTrackingPixel, isInteractionAnalyticsEnabled);
      setCategories(await getUpdatedCategories(view));
    });
  }, [
    UC,
    getUpdatedCategories,
    denyAllAmp,
    denyAllAmpLoading,
    denyAllServices,
    denyAllServicesLoading,
    isAmpEnabled,
    isInteractionAnalyticsEnabled,
    setCategories,
    view,
  ]);

  /**
   * A method to deny all tcf consents
   */
  const denyAllTCF = useCallback(async (): Promise<void> => {
    if (denyAllServicesLoading || denyAllForTCFLoading) {
      return;
    }

    const fromLayer =
      view === VIEW.FIRST_LAYER ? TCF_DECISION_UI_LAYER.FIRST_LAYER : TCF_DECISION_UI_LAYER.SECOND_LAYER;

    // For TCF we need to wait for both the services and the vendors/purposes to be updated before continuing.
    Promise.all([denyAllServices(), denyAllForTCF(fromLayer)]).then(async () => {
      if (isAmpEnabled) {
        await UC.denyAllAmp();
      }
      dispatchAnalyticsEvent(view, ANALYTICS_TYPE.DENY_ALL);
      setTrackingPixel(TRACKING_EVENT_TYPE.DENY_ALL, UC.setTrackingPixel, isInteractionAnalyticsEnabled);
      setCategories(await getUpdatedCategories(view));
      setTcfData(UC.getTCFData());
    });
  }, [
    UC,
    denyAllForTCF,
    denyAllForTCFLoading,
    denyAllServices,
    denyAllServicesLoading,
    getUpdatedCategories,
    isAmpEnabled,
    isInteractionAnalyticsEnabled,
    setCategories,
    setTcfData,
    view,
  ]);

  /**
   * A method to deny all consents
   * If UI_VARIANT not defined, method returns null
   */
  const denyAllConsents = useCallback((): Promise<void> | null => {
    switch (initialUIValues.variant) {
      case UI_VARIANT.CCPA:
        return acceptDenyCcpa(false);
      case UI_VARIANT.DEFAULT:
        return denyAllDefault();
      case UI_VARIANT.TCF:
        return denyAllTCF();
      default:
        return null;
    }
  }, [acceptDenyCcpa, denyAllDefault, denyAllTCF, initialUIValues.variant]);

  const enableScriptsForServicesWithConsent = useCallback((): void => {
    return UC.enableScriptsForServicesWithConsent();
  }, [UC]);

  /**
   * A method to get current active language
   */
  const getActiveLanguage = useCallback((): string => UC.getSettings().ui.language.selected, [UC]);

  /**
   * A method to get all services
   * @deprecated since version 2.2.9
   * Will be deleted in version 3.0.
   *
   * In case you need similar behavior, use getServicesBaseInfo or getServicesFullInfo
   *
   */
  const getServices = useCallback((): Service[] => UC.getServices(), [UC]);

  /**
   * A method to get all services with base information
   */
  const getServicesBaseInfo = useCallback((): BaseService[] => UC.getServicesBaseInfo(), [UC]);

  /**
   * A method to get all services with full information (aggregator call)
   */
  const getServicesFullInfo = useCallback((): Promise<Service[]> => UC.getServicesFullInfo(), [UC]);

  /**
   * A method to get all settings
   */
  const getSettings = useCallback((): Settings => UC.getSettings(), [UC]);

  /**
   * A method that exposes the TCF Vendors
   * If TCF is disabled, method returns undefined
   */
  const getTCFVendors = useCallback((): TCFVendor[] | undefined => {
    return UC.getTCFData()?.vendors;
  }, [UC]);

  /**
   * A method to check If UI is initialized
   */
  const isInitialized = useCallback((): boolean => isUIInitialized, [isUIInitialized]);

  /** A method that will open the firstLayer. */
  const showFirstLayer = useCallback(async (): Promise<void> => {
    return setView(VIEW.FIRST_LAYER);
  }, [setView]);

  /** A method that will open the secondLayer. */
  /** If an ID of a vendor or service is given it will directly open the right tab, scrolls to the entry and expands the service/vendor */
  const showSecondLayer = useCallback(
    async (scrollToServiceVendorId?: string | number): Promise<void> => {
      if (scrollToServiceVendorId) {
        setTabView(TAB_VIEW.SERVICES_VENDORS);
        setScrollToIdTabViewServiceVendor(scrollToServiceVendorId);
      } else {
        clearServiceVendorScrollToId();
      }

      return setView(VIEW.SECOND_LAYER);
    },
    [setView, setTabView, setScrollToIdTabViewServiceVendor, clearServiceVendorScrollToId],
  );

  /**
   * This method should always be triggered when selecting a new language in the language selector dropdown.
   * @param selectedLanguage the language to be changed to
   */
  const updateLanguage = useCallback(
    async (selectedLanguage: string): Promise<void> => {
      const setting = UC.getSettings();

      if (
        RTL_LANGUAGES.includes(setting.ui.language.selected) !== RTL_LANGUAGES.includes(selectedLanguage.toLowerCase())
      ) {
        onThemeChange(
          generateTheme(setting.ui, RTL_LANGUAGES.includes(selectedLanguage.toLowerCase()), initialUIValues.variant),
        );
      }

      if (!changeLanguageLoading) {
        changeLanguage(selectedLanguage.toLowerCase()).then(async () => {
          // When we fetch a new language, we do not want "unsaved" user choices to be lost.
          // That's why we get the current category choices before we set it
          setCategories(getCurrentUiCategoriesChoices(await getUpdatedCategories(view), categories));
          setSettings(UC.getSettings());
          setLanguage(selectedLanguage.toLowerCase());

          // TODO (EUD-344) keep yet unsaved toggle states for TCF, similar to the categories logic above.
          if (uiVariant === UI_VARIANT.TCF) {
            setTcfData(UC.getTCFData());
          }
        });
      }
    },
    [
      UC,
      categories,
      changeLanguage,
      changeLanguageLoading,
      initialUIValues.variant,
      getUpdatedCategories,
      onThemeChange,
      setCategories,
      setLanguage,
      setSettings,
      setTcfData,
      uiVariant,
      view,
    ],
  );

  useEffect(() => {
    window[UC_UI_GLOBAL_NAMESPACE] = {
      acceptAllConsents,
      acceptService: (serviceId: string) => acceptDenyService(serviceId, true),
      areAllConsentsAccepted,
      clearStorage,
      closeCMP,
      denyAllConsents,
      denyAndCloseCcpa: async () => {
        await acceptDenyCcpa(false);
        closeCMP();
      },
      enableScriptsForServicesWithConsent,
      getActiveLanguage,
      getServices,
      getServicesBaseInfo,
      getServicesFullInfo,
      getSettings,
      getTCFVendors,
      isInitialized,
      rejectService: (serviceId: string) => acceptDenyService(serviceId, false),
      restartCMP,
      showFirstLayer,
      showSecondLayer,
      updateLanguage,
    };
  }, [
    acceptAllConsents,
    acceptDenyCcpa,
    acceptDenyService,
    areAllConsentsAccepted,
    clearStorage,
    closeCMP,
    denyAllConsents,
    enableScriptsForServicesWithConsent,
    getActiveLanguage,
    getServices,
    getServicesBaseInfo,
    getServicesFullInfo,
    getSettings,
    getTCFVendors,
    isInitialized,
    restartCMP,
    showFirstLayer,
    showSecondLayer,
    updateLanguage,
  ]);

  useEffect(() => {
    if (window[UC_UI_GLOBAL_NAMESPACE] && shouldDispatchCustomEvent && isUIInitialized) {
      setShouldDispatchCustomEvent(false);
      window.dispatchEvent(new window.CustomEvent(GLOBAL_EVENTS.INITIALIZED));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [window[UC_UI_GLOBAL_NAMESPACE], shouldDispatchCustomEvent, isUIInitialized]);

  return {
    acceptAllDefault,
    acceptAllTCF,
    closeCMP,
    denyAllDefault,
    denyAllTCF,
    showSecondLayer,
    updateLanguage,
  };
};

export default usePublicMethods;
