/* eslint-disable no-undef */
import { useEffect, useState } from 'preact/hooks';
import { breakpoints } from '../styles';

export interface DeviceState {
  isDesktop: boolean;
  isIE: boolean;
  isLandscape: boolean;
  isMobile: boolean;
  isMobileExtraSmall: boolean;
  isMobileMd: boolean;
  isMobileSmall: boolean;
  isPortrait: boolean;
}

const getCurrentState = (): DeviceState => {
  const isIE = window.navigator.userAgent.indexOf('MSIE ') > 0 || window.navigator.userAgent.indexOf('Trident/') > 0;
  const isLandscape = window.matchMedia('all and (orientation:landscape)').matches;
  const isPortrait = window.matchMedia('all and (orientation:portrait)').matches;

  const windowSize = isPortrait ? window.innerWidth : window.innerHeight;

  const isDesktop = window.innerWidth > breakpoints.md;
  const isMobile = windowSize <= breakpoints.md;
  const isMobileMd = windowSize <= breakpoints.md && windowSize > breakpoints.sm;
  const isMobileSmall = windowSize <= breakpoints.sm && windowSize > breakpoints.xs;
  const isMobileExtraSmall = windowSize <= breakpoints.xs;

  return {
    isDesktop,
    isIE,
    isLandscape,
    isMobile,
    isMobileExtraSmall,
    isMobileMd,
    isMobileSmall,
    isPortrait,
  };
};

export const useDeviceState = (): DeviceState => {
  const [deviceState, setDeviceState] = useState<DeviceState>(getCurrentState());

  useEffect(() => {
    const handleResize = (): void => {
      setDeviceState(getCurrentState());
    };

    window.addEventListener('resize', handleResize, true);

    return (): void => {
      window.removeEventListener('resize', handleResize, true);
    };
  }, []);

  return deviceState;
};
