  DEFAULT_FONT_FAMILY,
  DEFAULT_FONT_SIZE,
  DEFAULT_INACTIVE_TOGGLE,
  DEFAULT_LAYER_BORDER_RADIUS,
  DEFAULT_LAYER_COLOR,
  DEFAULT_LIGHT_THEME_ACCENT_COLOR,
  DEFAULT_LIGHT_THEME_FONT_COLOR,
  DEFAULT_LIGHT_THEME_NEUTRAL_COLOR,
  DEFAULT_MAX_BANNER_WIDTH,
  DEFAULT_MAX_SIDE_AND_CENTER_CONTAINER_WIDTH,
  DEFAULT_OVERLAY_COLOR,
  DEFAULT_OVERLAY_OPACITY,
